<template>
    <div>
        <section id="why-us" class="wow fadeIn">
          <div class="container">
            <header class="section-header">
              <h3>¿Por qué elegirnos?</h3>
              <p>Contamos con el conocimiento para cubrir cualquier necesidad digital que tengas y la infraestructura necesaria para llevarla adelante.</p>
            </header>
  
            <div class="row row-eq-height justify-content-center">
  
              <div class="col-lg-4 mb-4">
                <div class="card wow bounceInUp">
                    <i class="fa fa-diamond"></i>
                  <div class="card-body">
                    <h5 class="card-title">Ideas y Conocimiento</h5>
                    <p class="card-text">Podemos entender las necesidades de un proyecto y generar una solución digital.</p>
                    <a href="#" class="readmore">Read more </a>
                  </div>
                </div>
              </div>
  
              <div class="col-lg-4 mb-4">
                <div class="card wow bounceInUp">
                    <i class="fa fa-language"></i>
                  <div class="card-body">
                    <h5 class="card-title">Humanizados</h5>
                    <p class="card-text">Dejamos de lado el concepto de “recurso” y conectamos con el lado humano de nuestro equipo, generando un ambiente de trabajo ameno que genera en los proyectos el mejor resultado.</p>
                    <a href="#" class="readmore">Read more </a>
                  </div>
                </div>
              </div>
  
              <div class="col-lg-4 mb-4">
                <div class="card wow bounceInUp">
                    <i class="fa fa-object-group"></i>
                  <div class="card-body">
                    <h5 class="card-title">Estructuras</h5>
                    <p class="card-text">Contamos con la estructura de hardware y software necesaria para cubrir las necesidades de un proyecto.</p>
                    <a href="#" class="readmore">Read more </a>
                  </div>
                </div>
              </div>
  
            </div>
  
            <div class="row counters" id="counters">
  
              <div class="col-lg-3 col-6 text-center">
                <iCountUp :options="options" :endVal="15" @ready="onReady"/>
                <p>Clientes</p>
              </div>
  
              <div class="col-lg-3 col-6 text-center">
                <iCountUp :options="options" :endVal="27" @ready="onReady"/>
                <p>Proyectos</p>
              </div>
  
              <div class="col-lg-3 col-6 text-center">
                <iCountUp :options="options" :endVal="2808" @ready="onReady"/>
                <p>Horas de soporte</p>
              </div>
  
              <div class="col-lg-3 col-6 text-center">
                <iCountUp :options="options" :endVal="5" @ready="onReady"/>
                <p style="text-decoration: line-through; margin-bottom:0;">Compañeros</p> 
                <p>Profesionales</p>
              </div>
      
            </div>
  
          </div>
        </section>
    </div>
</template>

<script>
import iCountUp from 'vue-countup-v2';
export default {
    name: 'Nosotros',
    components: {
      iCountUp
    },
    data() {
      return {
        delay: 1000,
        options: {
          useEasing: true,
          smartEasingThreshold: 10000,
          useGrouping: true,
          duration: 1
        }
      };
    },
    methods: {
      onReady: function(instance, CountUp) {
        const that = this;
        var num = that.endVal;
        var parametro = document.getElementById("counters");
        var param = parametro.offsetTop - 750;
        if (window.scrollY < param) {
          instance.reset();
        }
        window.addEventListener("scroll",function () {
          if ( window.scrollY >= param ) {
            instance.start();
          }
        })
      }
    }
}

</script>



