            <ul>
              <li class="active" @click="toggleMenu()">
                <router-link to="/" href="#" v-scroll-to="'#intro'" class="link">Inicio</router-link>
              </li>
              <li @click="toggleMenu()">
                <router-link to="/" href="#" v-scroll-to="'#about'" class="link">Sobre nosotros</router-link>
              </li>
              <li @click="toggleMenu()">
