        transition: all 0.2s;
        visibility: hidden;
        position:fixed;
        top:0;
        left:0;
        width: 100vw;
        height: 100vh;
        background: rgba(19, 39, 57, 0.8);
        ul {
          position:fixed;
          transition: left 0.5s;
          left: -260px;
          display: flex;
          padding-top: 24px;
          padding-left: 10px;
          flex-direction: column;
          width: 260px;
          background: rgba(19, 39, 57, 0.8);
          height: 100vh;
          .link {
            color: white !important;
            font-size: 16px;
            padding: 15px 15px;
            font-family: "Open Sans", sans-serif;
          }
          .link:hover {
