import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// <!-- Bootstrap CSS File -->
import '@/assets/NewBiz/lib/bootstrap/css/bootstrap.min.css' 

// <!-- Libraries CSS Files --> 
import '@/assets/NewBiz/lib/font-awesome/css/font-awesome.min.css'
import '@/assets/NewBiz/lib/animate/animate.min.css'
import '@/assets/NewBiz/lib/ionicons/css/ionicons.min.css'
import '@/assets/NewBiz/lib/owlcarousel/assets/owl.carousel.min.css'
import '@/assets/NewBiz/lib/lightbox/css/lightbox.min.css'

// <!-- Main Stylesheet File --> 
import '@/assets/NewBiz/css/style.css'

// <!-- JavaScript Libraries -->
import '@/assets/NewBiz/lib/bootstrap/js/bootstrap.bundle.min.js'
import '@/assets/NewBiz/lib/easing/easing.min.js'
import '@/assets/NewBiz/lib/mobile-nav/mobile-nav.js'
import '@/assets/NewBiz/lib/waypoints/waypoints.min.js'
import '@/assets/NewBiz/lib/isotope/isotope.pkgd.min.js'
import '@/assets/NewBiz/lib/lightbox/js/lightbox.min.js'

// // <!-- Contact Form JavaScript File -->
import '@/assets/NewBiz/contactform/contactform.js'

// // <!-- Template Main Javascript File -->
import '@/assets/NewBiz/js/main.js'

import VueScrollTo from 'vue-scrollto'
Vue.use(VueScrollTo, {
     duration: 1100,
     easing: [1.0, 0.0, 0.4, 0.4],
     offset: -50,
 })

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
