<template>
    <div id="imagenA" :class="{ visible: listo }">

        <div class="parrafo">
            <h1>Primera imagen</h1>
        </div>

    </div>
</template>

<script>
export default {
    name: 'imagenA',
    data() {
        return {
            listo : false
        }
    },
    mounted() {
        this.listo = true;
    }
}
</script>

<style lang="scss">
    #imagenA{
        position:relative;
        top: -20vw;
        height: 100vh;
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
        background: blue;
        color: white;
        opacity: 0;
        transition: all 0.5s;
        .parrafo {
            height: 40px;
        }
    }
    .visible {
        opacity: 1 !important;
        top:0 !important;
    }
</style>