<template>
    <div id="whatsapp">
        <!-- Boton -->
        <div class="boton-whap" @click="toggleWhap()" :class="{ 'boton-oculto': !oculto }">
            <img src="@/assets/img/whatsapp.png" class="img-fluid">
        </div>
        
        <!-- Ventana -->
        <form class="ventana-whap" :class="{ ocultado: oculto }">

            <div class="cabezera-whap">
                <div class="img-whap">
                    <img src="@/assets/logo.png" class="img-fluid" alt="">
                </div>
                <div class="persona-whap">
                    <h3>Sintony</h3>
                    <p>{{estado}}</p>
                </div>
            </div>
            
            <div class="btn-cerrar" @click="toggleWhap()">
              <div class="uno"></div>
              <div class="dos"></div>
              <div class="tres"></div>
              <div class="cuatro"></div>
            </div>

            <div class="form-group">
                <input type="text" placeholder="Nombre" class="form-control" v-model="whapNombre">
            </div>
            <div class="form-group">
                <textarea cols="30" rows="10" placeholder="Mensaje" class="form-control" v-model="whapTexto"></textarea>
            </div>
            <button type="button" class="btn btn-success btn-block " @click="enviarWhap()">Enviar</button>
        </form>
    </div>
</template>

<script>
export default {
    name: 'whapButton',
    data(){
        return {
            oculto: true,
            whapNombre: '',
            whapTexto: '',
            telefono: "+5492215648520",
            estado: 'escribiendo..',
            tiempo: 2000,
            max: 4000,
            min: 1000
        }
    },
    methods:{
        toggleWhap(){
            var anchura = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
            if (anchura > 768) {
                this.oculto = !this.oculto;
            }
            else {
                window.open("https://api.whatsapp.com/send?phone="+this.telefono+"&text=");
            }
        },
        enviarWhap(){
            var mensaje = "¡Hola me llamo "+this.whapNombre+"! y mi mensaje es: "+this.whapTexto;
            window.open("https://api.whatsapp.com/send?phone="+this.telefono+"&text="+mensaje);
        },
        cambiarTiempo(){
            this.tiempo = ( Math.floor(Math.random() * ( this.max - this.min )) ) + this.min;
        },
        cambiarEstado(){
            if (this.estado == "en linea") {
                this.estado = "escribiendo.."
            }
            else{
                this.estado = "en linea"
            }
            this.cambiarTiempo();
            setTimeout(this.cambiarEstado, this.tiempo);
        },
    },
    created() {
        this.cambiarEstado();
    }  
}
</script>

<style lang="scss">
    #whatsapp {
        .boton-whap {
            cursor: pointer;
            height: 43px;
            width: 43px;
            position: fixed;
            right: 15px;
            bottom: 14px;
            transition: all 1s;
        }

        .ventana-whap {
            background: url(../assets/img/fondowhap.jpg);
            background-size: cover;
            width: 350px;
            min-height: 460px;
            position: fixed;
            right: 65px;
            bottom: 0px;
            padding: 65px 25px 0px 25px;
            border-radius: 8px 8px 0px 0px;
            transition: bottom 0.5s;
            .cabezera-whap {
                position: absolute;
                width: 100%;
                height: 50px;
                top: 0;
                left: 0;
                background: #075e54;
                display: flex;
                align-items: center;
                border-radius: 7px 7px 0px 0px;
                .img-whap {
                    background: whitesmoke;
                    border-radius: 50%;
                    padding: 3px;
                    margin-left: 7px;
                    img {
                        max-height: 25px;
                    }
                }
                .persona-whap{
                    margin-left: 10px;
                    padding: 2px;
                    h3{
                        margin:0;
                        font-size: 16px;
                        color: white;
                    }
                    p {
                        margin:0;
                        color: white;
                        font-size: 13px;
                    }
                }
                
            }
            .btn-cerrar {
                position: absolute;
                cursor: pointer;
                padding: 15px;
                top: -3px;
                right: 0px;
                .uno, .dos, .tres, .cuatro {
                    filter: blur(0.4px);
                    height: 6px;
                    width: 15px;
                    border-radius: 3px;
                    background: indianred ;
                    transition: all 0.2s;
                }
                .uno {
                    transform: rotate(45deg) translate(-2px, 9px);
                }
                .dos {
                    transform: rotate(-45deg) translate(2px, 0px);
                }
                .tres {
                    transform: rotate(45deg) translate(3px, 0px);
                }
                .cuatro {
                    transform: rotate(-45deg) translate(-3px, -8px);
                }
            }
            .btn-cerrar:hover {
                .uno, .dos, .tres, .cuatro {
                    background: rgb(168, 34, 34) ;
                }
            }
            .btn-success {
                margin-top: 8%;
                border-color: #075e54;
                background: #075e54;
            }
            .btn-success:hover {
                border-color:#0b9484;
                background: #0b9484;
            }
        }
        .ocultado {
            bottom: -460px;
        }
        .boton-oculto {
            opacity: 0;
            visibility: hidden;
        }
    }
    
</style>